<template>
  <div>
    {{position}}
    {{count}}
    <button @click="getGeo()">getGeo</button>
  </div>
</template>

<script>
export default {
  data () {
    return {
      position: '',
      count: 0
    }
  },
  mounted () {
    this.initializeWrap()

    setInterval(() => {
    }, 1000)
  },
  methods: {
    initializeWrap () {
      if (window.wrapMode) {
        setTimeout(() => {
          /*
          if (window.StatusBar) {
            window.StatusBar.styleLightContent()
            // window.StatusBar.overlaysWebView(true)
          }
          */
          if (window.navigator && window.navigator.splashscreen) {
            window.navigator.splashscreen.hide()
          }
        }, 1000)
      }
    },
    getGeo () {
      navigator.geolocation.getCurrentPosition((position) => {
        console.log(position)
        this.position = 'lat:' + position.coords.latitude + ' long' + position.coords.longitude
        // this.position = 'Latitude: ' + position.coords.latitude + '\n' + 'Longitude: ' + position.coords.longitude + '\n' + 'Altitude: ' + position.coords.altitude + '\n' + 'Accuracy: ' + position.coords.accuracy + '\n' + 'Altitude Accuracy: ' + position.coords.altitudeAccuracy + '\n' + 'Heading: ' + position.coords.heading + '\n' + 'Speed: ' + position.coords.speed + '\n' + 'Timestamp: ' + position.timestamp + '\n'
        this.count++
      }, () => {
        this.position = 'error!!'
      })
    }
  }
}
</script>

<style lang="scss" scoped>
</style>



